<template>
  <div class="header">
   <div class="header-content">
      <div v-for="p in headerList" :key="p.id"
     @click="goDetail(p.url)"
     :class="{red:child == p.type}">{{ p.name }}</div>
   </div>

   <slot></slot>
  </div>
</template>


<script>
export default {
  props: ["child"],
  created() {
    // console.log(child)
  },
  data() {
    return {
      msg: "我的博客",
      headerList: [
        { name: "首页", url: "/", type: "index" },
        { name: "相册", url: "/photos", type: "photos" },
        { name: "日志", url: "/mylog", type: "mylog" },
        { name: "留言板", url: "/message", type: "message" },
        { name: "写博客", url: "/write", type: "write" },
        { name: "登陆", url: "login", type: "login" }
      ]
    };
  },
  methods: {
    goDetail(url) {
      this.$router.push({
        path: url,
        params:{
          name:333,

        },
        query:{
          name:'xiaodong',
          age:20,
          des:'长的非常帅'
        }
      });
    }
  }
};
</script>

<style scoped>
.header{
  width: 100%;
  background-color: #5b748d;
}
.header-content {
  width: 1000px;
  margin: 0 auto;
  display: flex;
  /* background-color: white; */
}
.header-content div {
  color: #0c0701;
  font-weight: 900;
  width: 80px;
  text-align: center;
  line-height: 40px;
}
.red {
  /* background-color:  rgba(218, 206, 39, 0.6); */
  color: white !important;
}
.header-content div:hover {
  /* background-color:  rgba(218, 206, 39, 0.6); */
  color: white;
}
</style>




